body, html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;

  font-size: 100%;
  line-height: 1.2;
  font-family: sans-serif;
}
body {
  background: -webkit-gradient(linear, left top, left bottom, from(#f0f9ff),color-stop(47%, #cbebff), to(#cedeff));
  background: -webkit-linear-gradient(top, #f0f9ff 0%,#cbebff 47%, #cedeff 100%);
  background: -o-linear-gradient(top, #f0f9ff 0%,#cbebff 47%, #cedeff 100%);
  background: linear-gradient(to bottom, #f0f9ff 0%,#cbebff 47%, #cedeff 100%);
}


.l-one-col {
  width: 80%;
  max-width: 1000px;
  margin-left: 10%;
  margin-right: 10%;
}

@media screen and (min-width: 999px) {
  .l-one-col {
    margin-left: auto;
    margin-right: auto;
  }
}


